<template>
  <div class="charts">
    <div id="myChart" :style="{width: '800px', height: '500px'}"></div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    mounted(){
      this.drawLine();
    },
    methods: {
      drawLine(){
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
          title: { text: '' },
          tooltip: {},
          xAxis: {
            data: ["1","2","3","4","5","6"]
          },
          yAxis: {},
          series: [{
            name: '',
            type: 'bar',
            data: [10, 2, 6, 17, 1, 7]
          }]
        });
      }
    }
  }
</script>

<style scoped>

</style>



